<script setup>
import { useRouter } from "vue-router";
const router = useRouter();
import Cookies from 'js-cookie'

import * as THREE from "three";
import { ref, reactive,onMounted } from "vue";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import { RGBELoader } from "three/examples/jsm/loaders/RGBELoader";


// 利用双向绑定实现切换场景  目前的方案存在缓存问题  暂时利用缓存数组下标 重新加载来绕开
const sceneArr = ['ersisd-Beerse_Kitchen_4k','ersisd-Merksem_Appartment_Living_4k','Living'];
const sceneIndex = ref(0)
function sceneIndexFnc() {
  sceneIndex.value = +sceneIndex.value+1>7?0:+sceneIndex.value+1
  Cookies.set('sceneIndex', sceneIndex.value)
  router.go(0)
}

if(Cookies.get('sceneIndex')!=undefined){
  sceneIndex.value = Cookies.get('sceneIndex')
}

//创建场景
const scene = new THREE.Scene();
//创建并添加相机
/* PerspectiveCamera( fov : Number, aspect : Number, near : Number, far : Number )
fov — 摄像机视锥体垂直视野角度
aspect — 摄像机视锥体长宽比
near — 摄像机视锥体近端面
far — 摄像机视锥体远端面 */
const camera = new THREE.PerspectiveCamera(
  75,
  window.innerWidth / window.innerHeight,
  0.1,
  1000
);
camera.position.z = 0.1; //相机位置


// 设置渲染器并渲染场景 
const renderer = new THREE.WebGLRenderer();//初始化渲染器
renderer.setSize(window.innerWidth, window.innerHeight);//设置渲染的尺寸大小

const container = ref(null);

const render = () => {
  renderer.render(scene, camera);//使用渲染器，通过相机将场景渲染进来 
  requestAnimationFrame(render);
};


// 环境提如图资源过大  加载太慢不利于展示  优化为图片小得多的六面图
// const geometry = new THREE.SphereGeometry(5, 32, 32);
// const loader = new RGBELoader();// HDR环境图片加载
// loader.load(`./imgs/hdr/${sceneArr[sceneIndex.value]}.hdr`, (texture) => {
//   const material = new THREE.MeshBasicMaterial({ map: texture });
//   const sphere = new THREE.Mesh(geometry, material);
//   sphere.geometry.scale(1, 1, -1);
  // scene.add(sphere);
// }); 


// 六面图方案  将六面图通过材质加载器贴在几何体表面  设置相机位置后查看物体内部即可实现
const geometry = new THREE.BoxGeometry(10,10,10);
let arr = [
  ['4_l','4_r','4_u','4_d','4_b','4_f'],
  ['7_l','7_r','7_u','7_d','7_b','7_f'],
  ['0_l','0_r','0_u','0_d','0_b','0_f'],
  ['2_l','2_r','2_u','2_d','2_b','2_f'],
  ['11_l','11_r','11_u','11_d','11_b','11_f'],
  ['19_l','19_r','19_u','19_d','19_b','19_f'],
  ['20_l','20_r','20_u','20_d','20_b','20_f'],
  ['23_l','23_r','23_u','23_d','23_b','23_f'],
]
let boxMaterials = []
arr[sceneIndex.value].forEach(it=>{
  let texture = new THREE.TextureLoader().load(`./imgs/${it}.jpg`);
  if(it.includes('_u')||it.includes('_d')){
    texture.rotation = Math.PI;
    texture.center = new THREE.Vector2(0.5,0.5)
  }
  boxMaterials.push(new THREE.MeshBasicMaterial({map:texture}));
})
const cube = new THREE.Mesh(geometry,boxMaterials);
// cube.material = boxMaterials;
cube.geometry.scale(1,1,-1)
scene.add(cube);



window.addEventListener("dblclick",()=>{//双击屏幕全屏/取消全屏
    document.fullscreenElement?document.exitFullscreen():renderer.domElement.requestFullscreen()
})

onMounted(() => {
  renderInit();
});

function renderInit() {
  const controls = new OrbitControls(camera, container.value);//轨道控制器
  controls.enableDamping = true;//设置控制器阻尼
  container.value.appendChild(renderer.domElement);//将webgl渲染的内容添加到body
  render();
}


// 监听画面变化渲染画面  
window.addEventListener('resize',()=>{
    camera.aspect = window.innerWidth/window.innerHeight//更新相机
    camera.updateProjectionMatrix();//更新相机投影矩阵
    renderer.setSize(window.innerWidth,window.innerHeight)//更新渲染器尺寸大小
    renderer.setPixelRatio(window.devicePixelRatio)//设置渲染器像素比————就是缩放比
})

</script>

<template>
  <div>
    <div class="container" ref="container">
    </div>
    <button class="switchover" @click="sceneIndexFnc">下一场景</button>
  </div>
</template>

<style>
* {
  margin: 0;
  padding: 0;
}
.container {
  height: 100vh;
  width: 100vw;
  background-color: #f0f0f0;
  position:relative;
}
.switchover{
  width: 140px;
  height: 60px;
  background-color: #ffffff;
  border: 1px solid #35a8ff;
  z-index:99;
  position:absolute;
  bottom:40px;
  right:40px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>
